.top {
  // padding: 30px 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    padding-top: 150px;
    // // padding-top: 14vh;
    // padding-bottom: 10vh;

  }
  &__title {
    font-weight: 700;
    font-size: 60px;
    max-width: 600px;
    margin-bottom: 30px;
  }
  &__subtitle {
    font-size: 25px;
    margin-bottom: 60px;
  }
  &__btn {

  }
  &__img {
    max-width: 660px;
    position: relative;
  }
  &__price {
    position: absolute;
    font-size: 50px;
    font-weight: 700;
    left: 25%;
    top: 45%;
    &::after {
      content: '';
      display: inline-block;
      margin-left: 5px;
      background: no-repeat center / contain;
      height: .7em;
      width: 1em;
      background-image: url('../img/icon/rouble-red.svg');
    }
  }
}


