@media (max-width: 1024px) {
  .top {
    &__inner {
      flex-direction: column;
    }
    &__content {
      position: relative;
      z-index: 2;
    }
    &__title {
      font-size: 70px;
      max-width: 650px;
    }
    &__subtitle {
      font-size: 35px;
    }
    &__btn {
      font-size: 35px;
    }
    &__img {
      margin-right: 0;
      margin-left: auto;
      position: relative;
      z-index: 1;
      margin-top: -200px;
      width: 80%;
      max-width: none;
    }
  }
}
