.contacts {
  padding: 100px 0;
  &__inner {}
  &__title {
    color: #222122;
  }
  &__row {
    display: flex;
    align-items: flex-start;
  }
  &__box {
    display: flex;
    margin-right: 175px;
  }
  &__icon {
    width: 40px;
    height: 40px;
    margin-right: 30px;
  }
  &__list {}
  &__item {}
  &__item--more {
    margin-top: 25px;
    font-weight: 500;
  }
  &__socials {
    & .socials__link {
      display: block;
      width: 40px;
      height: 40px;
    }
    & .socials__icon {
      width: 40px;
      height: 40px;
    }
    & .socials__link + .socials__link {
        margin-left: 30px;
    }
  }
}


@media (min-width: 1300px) and (max-width: 1600px) {
  .contacts {
    padding: 5vw 0;
    &__row {
      width: 100%;
    }
    
    &__box {
      margin-right: 0;
      & + & {
        // margin-left: 7%;
        margin-left: 6.77vw;
      }
      // margin-right: 140px;
    }
    &__icon {
      width: 2vw;
      height: 2vw;
      margin-right: 1.5vw;
    }
    &__item--more {
      margin-top: 1.25vw;
    }
    &__socials {
      & .socials__link {
        width: 2vw;
        height: 2vw;
      }
      & .socials__icon {
        width: 2vw;
        height: 2vw;
      }
      & .socials__link + .socials__link {
        margin-left: 1vw;
      }
    }

  }
}

@media (max-width: 1299px) {
  .contacts {
    &__row {
      justify-content: space-between;
    }
    &__box {
      margin-right: 0;
    }
  }
}

@media (max-width: 1024px) {
  .contacts {
    font-size: 20px;
  }
}
