@media (min-width: 768px) and (max-width: 900px) {
  .contacts {
    &__row {
      flex-wrap: wrap;
    }
    &__box {
      flex-basis: 45%;
    }
    &__socials {
      margin-top: 20px;
    }
  }
}

@media (max-width: 767px) {
  .contacts {
    padding-top: 75px;
    padding-bottom: 60px;
    &__row {
      flex-direction: column;
    }
    &__box {
      // width: 100%;
      margin-bottom: 60px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__icon {
      // width: 40px;
      // height: 40px;
    }
  }
}
